<!-- 装桶月统计报表 -->
<template>
  <div>
    <Row>
      <Select style="width:100px" placeholder="年份">
        <Option v-for="item in yearList" :value="item.value" :label="item.label" :key="item.value"></Option>
      </Select>
      <Select style="width:100px" placeholder="月份" class="margin-left">
        <Option v-for="item in monthList" :value="item.value" :label="item.label" :key="item.value"></Option>
      </Select>
      <Select style="width:100px" placeholder="部门" class="margin-left">
        <Option
          v-for="item in departmentList"
          :value="item.value"
          :label="item.label"
          :key="item.value"
        ></Option>
      </Select>展现形式
      <Checkbox v-model="date">日期</Checkbox>
      <Checkbox v-model="division">科室</Checkbox>
      <Button type="success" class="margin-left">
        <Icon type="md-search" />确定
      </Button>
    </Row>
    <br />
    <Table :columns="column1" :data="data1"></Table>
  </div>
</template>

<script>
export default {
  name: "BarrellMonth",
  data() {
    return {
      column1: [
        {
          title: "日期",
          key: "date",
          align: "center"
        },
        {
          title: "科室",
          key: "division",
          align: "center"
        },
        {
          title: "重量",
          key: "kg",
          align: "center"
        },
        {
          title: "数量",
          key: "num",
          align: "center"
        }
      ],
      data1: []
    };
  }
};
</script>

